import './styles.css';
import template from './templates/template.hbs';
import listTemplate from './templates/listTemplate.hbs';
import fetchImages from './apiService';
import { alert, notice, info, success, error } from '@pnotify/core';
import murkupCreator from './murkup';
var debounce = require('debounce');


const key = "19318946-e9404c8a1dbf4b4ee541352f1";
let pageNumber = 1;
const inputEl = document.querySelector('[name="query"]');
const btnLoadMore = document.createElement("button")
const body = document.querySelector("body");
const galleryEl = document.querySelector('.gallery');
inputEl.addEventListener('input', debounce(onInput, 800))


if(localStorage.getItem("searchField") !== "") {
   inputEl.value = localStorage.getItem("searchField");
   fetchImages(inputEl.value, pageNumber, key)
   .then(data=> murkupCreator(data, galleryEl))
}

function onInput(e) {
   e.preventDefault();
   pageNumber = 1;
   galleryEl.innerHTML = "";
   localStorage.setItem("searchField", inputEl.value)
   fetchImages(inputEl.value, pageNumber, key)
   .then(data=>murkupCreator(data, galleryEl))
}



body.appendChild(btnLoadMore)
btnLoadMore.innerHTML = "load more"
btnLoadMore.addEventListener("click", onClick);

function onClick (e) {

   e.preventDefault();
 
   pageNumber+=1;
   fetchImages(inputEl.value, pageNumber, key)
   .then(data=>murkupCreator(data))
   window.scrollTo({
      top: 10,
      behavior: 'smooth'
  });
}
